.Button {
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 10px;
    font-weight: bold;
    font-size: 22px;
}

.Button:first-of-type {
    margin-left: 0;
    padding-left: 0;
}
.Button:disabled{
    color: #ccc;
    cursor: not-allowed;
}

.Red{
    color: rgb(209, 6, 6);
}

.Green{
    color: rgb(14, 121, 14);
}

.Primary{
    color: rgb(10, 10, 95);
}
.Button_2{
    padding: 2px !important;
    font-weight: normal !important;
    font-size: 15px !important;
    margin: 0 !important;
}